import React from "react";
import Header from "../../components/Header/header";
import SpecialFooter from "../../components/SpecialFooter/SpecialFooter";

function Projects() {
    return (
        <body class="colorscheme-auto">
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container centered">
                    <div className="options">
                        <h1>Projects</h1>
                        <ul>
                            <li>
                                <a href='/projects/university'>
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>University</span>
                                    </button>
                                </a>
                            </li>
                            <li>
                                <a href='/projects/fiverr'>
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>Fiverr</span>
                                    </button>
                                </a>
                            </li>
                            <li>
                                <a href='/projects/personal'>
                                    <button className="button" style={{"vertical-align": "middle"}}>
                                        <span>Personal</span>
                                    </button>
                                </a>
                            </li>
                        </ul>
                    </div>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default Projects;